.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.gunungan-2 {
		width: 76px;
		margin: 0 auto 2rem;
		@include max-lg {
			width: 50px;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		.orn-3 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: scaleY(-1);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			img {
				transform-origin: 50% 100%;
			}
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 0;
			width: 180px;
			transform: translate(-20%, -30%) scaleY(-1) rotate(15deg);
