	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		z-index: 3;
		margin-bottom: 2rem;
		img {
			width: 76px;
			height: auto;
			@include max-lg {
				width: 50px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		margin-bottom: 2rem;
		.image-element {
			width: 100%;
			height: 100%;
			padding: 2px;
			background: $stroke-gradient;
			mask-image: url(../images/mask-photo.png);
			mask-size: 100% 100%;
			mask-position: center;
			mask-repeat: no-repeat;
			.couple-background {
				position: relative;
				width: 100%;
				height: 100%;
				mask-image: url(../images/mask-photo.png);
				mask-size: 100% 100%;
				mask-position: center;
				mask-repeat: no-repeat;
			}
		}
	}
	.couple-content {
		position: relative;
		border-radius: var(--border-radius);
		background-color: var(--color-primary);
		border: 2px solid $border-gold;
		padding: 3rem 2rem;
		margin-bottom: 2rem;
		color: var(--color-dark);
		@include max-md {
			padding: 2rem 1rem;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			aspect-ratio: 3/4;
			height: auto;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 2px;
				background: $stroke-gradient;
				mask-image: url(../images/mask-photo.png);
				mask-size: 100% 100%;
				mask-position: center;
				mask-repeat: no-repeat;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					mask-image: url(../images/mask-photo.png);
					mask-size: 100% 100%;
					mask-position: center;
					mask-repeat: no-repeat;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--text-button);
				background-color: var(--color-secondary);
				width: fit-content;
				padding: 0.5rem 0.7rem;
				border-radius: 50px;
				text-decoration: none;
				display: block;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
	.reminder-wrap {
		padding: 3rem 0 5rem;
		@include max-md {
			padding: 3rem 0;
		}
		.elements-wrapper-absolute {
			height: calc(100% + 5rem);
		}
	}
