.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, 100%) scaleX(-1) rotate(-15deg);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-3 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translate(30%, -50%) scaleX(-1);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
		}
		@include max-xs {
			width: 80px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 25%;
		right: 0;
		width: 180px;
		transform: translateX(40%) rotate(-15deg);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(0%, 30%) scaleY(-1) rotate(-25deg);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 80%;
		left: 0;
		width: 180px;
		transform: translateX(-50%) scaleX(-1) rotate(-15deg);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-20%, 40%) scaleY(-1) rotate(-25deg);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
		}
	}
	.orn-5 {
		position: absolute;
		top: 83%;
		right: 0;
		width: 180px;
		transform: translateX(50%) rotate(-30deg) scaleX(-1);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(20%, 50%) rotate(-20deg);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
		}
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			padding: 3rem 2rem;
			border: 3px solid $border-gold;
			border-radius: var(--border-radius);
			background: var(--color-primary);
			color: var(--color-dark);
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				width: fit-content;
				max-width: 100%;
				margin: 0 auto;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
			}
			.card {
				border: none;
				background: $stroke-gradient;
				padding: 2px;
				height: 100%;
				text-align: center;
				border-radius: 5px;
				mask-image: url(../images/mask-card.png);
				mask-size: 100% 100%;
				mask-position: top;
				mask-repeat: no-repeat;
				.card-body {
					position: relative;
					background-color: var(--color-main);
					mask-image: url(../images/mask-card.png);
					mask-size: 100% 100%;
					mask-position: top;
					mask-repeat: no-repeat;
					border-radius: 5px;
					padding: 8rem 2rem 3rem;
					&::before {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 80%;
						background: linear-gradient(180deg, rgba(255, 255, 255, 0.2), transparent);
						content: "";
					}
					@include max-md {
						padding: 5rem 1rem 2rem;
					}
					h4 {
						font-weight: 600;
						margin: 1rem 0;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin-top: 3rem;
		position: relative;
		padding: 3rem 1rem;
		border-radius: var(--border-radius);
		text-align: center;
		z-index: 2;
		background: var(--color-primary);
		border: 2px solid $border-gold;
		color: var(--color-dark);
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue-sm {
		@include max-md {
			gap: 3rem;
		}
	}
	.event-name {
		position: relative;
		width: 100%;
		.date {
			width: 100%;
			text-align: center;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
			div {
				position: relative;
			}
		}
		h4 {
			font-family: $font-secondary;
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
