.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, 0%) scaleX(-1) rotate(-15deg);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(0%, 40%) scaleY(-1) rotate(-25deg);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 5%;
		right: 0;
		width: 180px;
		transform: translateX(40%) rotate(-15deg);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(0%, 30%) scaleY(-1) rotate(-25deg);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 25%;
		left: 0;
		width: 180px;
		transform: translateX(-50%) rotate(25deg);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 25%;
		right: 0;
		width: 140px;
		transform: translateX(40%) rotate(-15deg);
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			width: 100px;
		}
		@include max-xs {
			width: 80px;
		}
		img {
			transform-origin: 50% 100%;
		}
	}
	.title-section {
		color: var(--color-light);
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		z-index: 5;
		.stream-content {
			position: relative;
			background: $stroke-gradient;
			mask-image: url(../images/mask-card.png);
			mask-size: 100%;
			mask-position: top;
			mask-repeat: no-repeat;
			padding: 2px;
			margin-bottom: 2rem;
			border-radius: 5px;
			.stream-description {
				position: relative;
				padding: 7rem 2rem 3rem;
				background: var(--color-main);
				mask-image: url(../images/mask-card.png);
				mask-size: 100%;
				mask-position: top;
				mask-repeat: no-repeat;
				border-radius: 5px;
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 80%;
					background: linear-gradient(180deg, rgba(255, 255, 255, 0.2), transparent);
					content: "";
				}
				@include max-md {
					padding: 5rem 1rem 2rem;
				}
				.yt-description {
					position: relative;
					margin-top: 3rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
}
