.countdown {
	display: flex;
	text-align: center;
	justify-content: space-evenly;
	position: relative;
	margin: 0 auto;
	width: fit-content;
	gap: 1rem;
	z-index: 3;
	@include max-md {
		gap: 0.5rem;
	}
	@include max-rwd(350px) {
		display: grid;
		grid-template-columns: 1fr 1fr;
		gap: 1rem;
	}
	.days,
	.hours,
	.minutes,
	.seconds {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		position: relative;
		background: var(--color-primary);
		border-radius: var(--border-radius);
		color: var(--color-dark);
		padding: 1rem 0.25rem;
		min-width: 90px;
		@include max-md {
			min-width: 60px;
			padding: 1rem 0.75rem;
		}
		@include max-xs {
			min-width: 63px;
		}
		@include max-rwd(350px) {
			min-width: 72px;
		}
		.huruf {
			font-size: 14px;
			font-weight: 600;
			@include max-xs {
				font-size: 12px;
			}
		}
		.angka {
			margin-bottom: 0;
			font-weight: 500;
			font-size: 32px;
			font-family: $font-secondary;
			@include max-md {
				font-size: 26px;
			}
			@include max-xs {
				font-size: 24px;
			}
		}
		.huruf,
		.angka {
			margin: 0;
			padding: 0;
		}
	}
}
