.story-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
			z-index: 3;
		}
		@include max-xs {
			width: 100px;
		}
		.orn-3 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: scaleY(-1);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 0;
			width: 180px;
			transform: translate(-20%, -30%) scaleY(-1) rotate(15deg);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: scaleX(-1);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
			z-index: 3;
		}
		@include max-xs {
			width: 100px;
		}
		.orn-3 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: scaleY(-1);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 0;
			width: 180px;
			transform: scaleY(-1);
			transform: translate(-20%, -30%) scaleY(-1) rotate(15deg);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
			}
			@include max-xs {
				width: 120px;
			}
		}
	}
	.story-content {
		position: relative;
		padding-top: 5rem;
	}
	.story-description {
		position: relative;
		text-align: center;
		background: var(--color-primary);
		padding: 3rem 2rem;
		color: var(--color-dark);
		border-radius: var(--border-radius);
		border: 2px solid $border-gold;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 16/9;
		height: auto;
		overflow: hidden;
		@include max-md {
			aspect-ratio: 4/3;
		}
		.story-background {
			position: relative;
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
