.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100px;
		transform: translate(-50%, 30%);
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 280px;
		transform: translate(-40%, 30%) rotate(30deg);
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 200px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-3 {
			position: absolute;
			top: 0;
			right: 0;
			width: 180px;
			transform: translate(70%, 5%);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
				transform: translate(20%, 25%);
			}
			@include max-xs {
				width: 120px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 280px;
		transform: translate(40%, 30%) rotate(15deg);
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 200px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-5 {
			position: absolute;
			top: 0;
			left: 0;
			width: 180px;
			transform: translate(-25%, 35%) rotate(-25deg);
			@include max-lg {
				width: 160px;
			}
			@include max-md {
				width: 140px;
				transform: translate(10%, 35%) rotate(-25deg);
			}
			@include max-xs {
				width: 120px;
			}
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 4rem 0 3rem;
		@include max-lg {
			padding: 4rem 0 7rem;
		}
		.cover-content {
			text-align: center;
			position: relative;
			width: auto;
			height: fit-content;
			background: $stroke-gradient;
			mask-image: url(../images/mask-photo.png);
			mask-size: 100% 100%;
			mask-position: center;
			mask-repeat: no-repeat;
			padding: 2px;
			aspect-ratio: 3/4;
			@include max-xl {
				width: 50%;
			}
			@include max-lg {
				width: 70%;
			}
			@include max-md {
				width: 100%;
			}
			.cover-body {
				position: relative;
				height: fit-content;
				background: var(--color-main);
				mask-image: url(../images/mask-photo.png);
				mask-size: 100% 100%;
				mask-position: center;
				mask-repeat: no-repeat;
				&::before {
					position: absolute;
					inset: 0;
					background: rgba($color: #000000, $alpha: 0.4);
					content: "";
					z-index: 2;
				}
				@include max-lg {
					height: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
				}
				.cover-description {
					height: fit-content;
					position: relative;
					padding: 8rem 3rem;
					text-align: center;
					z-index: 5;
					@include max-md {
						padding: 2rem 0 1rem;
					}
					.greeting-wrapper {
						position: relative;
						@include max-md {
							font-size: 14px;
						}
						p {
							font-weight: 700;
						}
					}
					h6 {
						font-weight: 600;
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
