.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	.scroll-icon {
		position: relative !important;
		z-index: 10;
	}
	.elements-wrapper-absolute {
		overflow: hidden;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: scaleX(-1);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 140px;
		transform: translate(-50%, 20%);
		z-index: 3;
		@include min-xxl {
			width: 160px;
		}
		@include max-md {
			width: 100px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 300px;
		transform: translate(-30%, 40%) rotate(15deg);
		z-index: 3;
		@include min-xxl {
			width: 340px;
		}
		@include max-lg {
			width: 240px;
			transform: translate(-40%, 35%) rotate(15deg);
		}
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			transform: translate(-50%, 40%) rotate(15deg);
			width: 200px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-5 {
			position: absolute;
			top: 0;
			right: 0;
			width: 220px;
			transform: translate(160%, 0);
			@include min-xxl {
				width: 240px;
			}
			@include max-xl {
				transform: translate(90%, 0);
			}
			@include max-lg {
				width: 180px;
				transform: translate(50%, 20%);
			}
			@include max-md {
				width: 160px;
				transform: translate(40%, 20%);
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 300px;
		transform: translate(25%, 40%) rotate(15deg);
		z-index: 3;
		@include min-xxl {
			width: 340px;
		}
		@include max-lg {
			width: 240px;
			transform: translate(25%, 40%) rotate(15deg);
		}
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 200px;
			transform: translate(30%, 45%) rotate(15deg);
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-7 {
			position: absolute;
			top: 0;
			left: 0;
			width: 220px;
			transform: translate(-130%, 45%) rotate(-15deg);
			@include min-xxl {
				width: 240px;
			}
			@include max-xl {
				transform: translate(-60%, 40%) rotate(-15deg);
			}
			@include max-lg {
				width: 180px;
				transform: translate(-30%, 30%);
			}
			@include max-md {
				width: 160px;
				transform: translate(-10%, 30%);
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.header-content {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 4rem 0 0;
		height: 100%;
		.header {
			position: relative;
			display: block;
			width: 40%;
			padding: 6rem 0 0;
			height: 100%;
			background: $stroke-gradient;
			mask-image: url(../images/mask-card.png);
			mask-size: 100% 100%;
			mask-position: top;
			mask-repeat: no-repeat;
			&::before {
				position: absolute;
				inset: 2px;
				background: var(--color-main);
				mask-image: url(../images/mask-card.png);
				mask-size: 100% 100%;
				mask-position: top;
				mask-repeat: no-repeat;
				content: "";
			}
			&::after {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 80%;
				background: linear-gradient(180deg, rgba(255, 255, 255, 0.2), transparent);
				content: "";
			}
			@include max-xl {
				width: 50%;
			}
			@include max-lg {
				width: 80%;
			}
			@include max-md {
				width: 90%;
			}
			.header-title {
				position: relative;
				text-align: center;
				z-index: 3;
				color: $color-custom;
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
