.thank-section {
	position: relative;
	padding: 5rem 0 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 160px;
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 160px;
		transform: scaleX(-1);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100px;
		transform: translate(-50%, 30%);
		z-index: 3;
		@include max-lg {
			width: 80px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 280px;
		transform: translate(-30%, 40%) rotate(15deg);
		z-index: 3;
		@include max-lg {
			width: 240px;
			transform: translate(-40%, 40%) rotate(15deg);
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-5 {
			position: absolute;
			top: 0;
			right: 0;
			width: 200px;
			transform: translate(130%, 0);
			@include max-lg {
				width: 180px;
				transform: translate(50%, 20%);
			}
			@include max-md {
				width: 160px;
				transform: translate(50%, 10%);
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 280px;
		transform: translate(25%, 40%) rotate(15deg);
		z-index: 3;
		@include max-lg {
			width: 240px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-7 {
			position: absolute;
			top: 0;
			left: 0;
			width: 200px;
			transform: translate(-120%, 45%) rotate(-15deg);
			@include max-lg {
				width: 180px;
				transform: translate(-10%, 30%) rotate(-25deg);
			}
			@include max-md {
				width: 160px;
				transform: translate(-10%, 20%) rotate(-25deg);
			}
			@include max-xs {
				width: 140px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 360px;
		height: auto;
		margin: 0 auto;
		@include max-md {
			width: 100%;
		}
		.image-element {
			width: 80%;
			height: auto;
			aspect-ratio: 3/4;
			margin: 0 auto 2rem;
			@include max-md {
				width: 70%;
			}
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 500px;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		padding: 5rem 0;
		.thank {
			position: relative;
			background: $stroke-gradient;
			mask-image: url(../images/mask-card.png);
			mask-size: 100%;
			mask-position: top;
			mask-repeat: no-repeat;
			padding: 2px;
			.thank-body {
				position: relative;
				background: var(--color-main);
				mask-image: url(../images/mask-card.png);
				mask-size: 100%;
				mask-position: top;
				mask-repeat: no-repeat;
				padding: 8rem 2rem 3rem;
				@include max-md {
					padding: 5rem 1rem 2rem;
				}
				.title-section {
					color: var(--color-light);
				}
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
