.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 180px;
		transform: translate(-50%, -50%) rotate(35deg);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(80%, 50%) rotate(-15deg);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
		}
	}
	.orn-2 {
		position: absolute;
		top: 80%;
		right: 0;
		width: 180px;
		transform: translateX(50%) rotate(-35deg) scaleX(-1);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 50% 100%;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(-80%, -40%) rotate(-20deg);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
		}
	}
	.rsvp-wrapper {
		position: relative;
		background: $stroke-gradient;
		border-radius: 5px;
		padding: 2px;
		mask-image: url(../images/mask-card.png);
		mask-size: 100%;
		mask-position: top;
		mask-repeat: no-repeat;
		.title-section {
			color: var(--color-light);
		}
		.rsvp-form {
			padding: 8rem 3rem 3rem;
			position: relative;
			background: var(--color-main);
			mask-image: url(../images/mask-card.png);
			mask-size: 100%;
			mask-position: top;
			mask-repeat: no-repeat;
			&::before {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 80%;
				background: linear-gradient(180deg, rgba(255, 255, 255, 0.2), transparent);
				content: "";
			}
			@include max-md {
				padding: 5rem 1rem 2rem;
			}
		}
	}
}
